import React, { useImperativeHandle } from "react";
import { Link, withRouter } from "react-router-dom";

import { isLogged, getStorageItem, removeStorageItem, setStorageItem, shop } from "../config/config";
import Api from "../config/Api";

import Header from "../components/Header";
import Footer from "../components/Footer";
import Popup from "../components/Popup";

import "../styles/admin.css";

class Admin extends React.Component {

    state = {
        offset: 0,

        user: getStorageItem("user"),

        name: "",
        description: "",
        price: "",
        imagePath: "",
        image: null,

        type: 1,
        category: 1,

        loading: true,
        title: "",
        popup: false,

        categories: [
            "Single Oils",
            "Proprietary Blends",
            "Roll-on Essentials"
        ]
    }

    constructor() {
        super();

        this.addProduct = this.addProduct.bind(this);
        this.changeImage = this.changeImage.bind(this);
        this.updateType = this.updateType.bind(this);
        this.updateCategory = this.updateCategory.bind(this);
    }

    async addProduct() {
        this.setState({ popup: true, loading: true })

        const { name, description, price, image, type, category } = this.state
        
        const token = getStorageItem("token")

        console.log(type, category)

        const addProduct = await Api.createProduct({
            type: type,
            category: category,
            eshop: true,
            name: name,
            description: description,
            price: price
        }, token)

        console.log(addProduct)

        if (addProduct.success === "New product created successfully") {
            const addImage = await Api.addImage(addProduct.product._id, image, token);

            console.log(addImage);
            this.setState({ popup: true, loading: false, title: "Produkt spene pridan do e-shopu" })
        } else {
            this.setState({ popup: true, loading: false, title: "Produkt sa nepodarilo prida do e-shopu" })
        }
    }

    updateType() {
        this.setState({ type:parseInt(document.getElementById("type-select").value) })
    }

    updateCategory() {
        this.setState({ category: parseInt(document.getElementById("category-select").value) })
    }

    changeImage() {
        const photo = document.getElementById("file").files[0];
        this.setState({ image: photo, imagePath: window.URL.createObjectURL(photo) })
    }

    componentDidMount() {
        this.setState({ offset: document.getElementById("header").clientHeight });
        window.addEventListener('resize', this.updateOffset.bind(this));

        if (!isLogged()) {
            this.props.history.push("/prihlasenie")
        }
    }

    componentDidUpdate() {
        if (!isLogged()) {
            this.props.history.push("/prihlasenie")
        }
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.updateOffset.bind(this));
    }

    updateOffset() {
        this.setState({ offset: document.getElementById("header").clientHeight });
        this.forceUpdate();
    }

    render() {
        return(
            <div className="screen" id="admin">
                <Header />

                {this.state.popup ? (
                    <Popup
                        type="info"
                        title={this.state.title}
                        onClick={() => {
                            this.setState({ popup: false })
                            this.props.history.push("/profil")
                        }}
                        loading={this.state.loading}
                    />
                ) : null}

                <div className="content" style={{ paddingTop: this.state.offset + 50 }}>
                    <div className="title">Administrcia e-shopu</div>

                    <div className="section">Prida produkt</div>
                    <div className="text">
                        Pridajte produkt do databzy produktov pre e-shop TerraMia. Potrebn je meno produktu, popis produktu, cena a obrzok. 
                    </div>

                    <div className="details">
                        <div className="heading">Meno produktu</div>
                        <input className="field" type="text" value={this.state.name} placeholder="Meno produktu" onChange={(event) => this.setState({ name: event.target.value })} />
                        <div className="heading">Popis</div>
                        <textarea className="area" rows="10" value={this.state.description} placeholder="Popis produktu" onChange={(event) => this.setState({ description: event.target.value })}></textarea>

                        <div className="heading">Typ produktu</div>
                        <select class="select" id="type-select" onChange={() => this.updateType()}>
                            <option value="1">{shop[0].type}</option>
                            <option value="2">{shop[1].type}</option>
                            <option value="3">{shop[2].type}</option>
                            <option value="4">{shop[3].type}</option>
                            <option value="5">{shop[4].type}</option>
                            <option value="6">{shop[5].type}</option>
                        </select>

                        <div className="heading">Kategria produktu</div>
                        <select class="select" id="category-select" onChange={() => this.updateCategory()}>
                            {shop[this.state.type - 1].categories.map((category, index) => <option value={index + 1}>{category}</option>)}
                        </select>

                        <div className="heading">Cena (v centoch)</div>
                        <input className="field" type="text" value={this.state.price} placeholder="Cena v centoch" onChange={(event) => this.setState({ price: event.target.value })} />

                        <div className="heading">Fotka</div>
                        <label className="button-filled" for="file">Vybra sbor</label>
                    </div>

                    <input className="file-input" name="file" id="file" type="file" onChange={() => this.changeImage()} />

                    <img className="image" id="product-image" src={this.state.imagePath} style={this.state.imagePath ? {} : { display: "none" }} />

                    <div className="button-filled" onClick={() => this.addProduct()}>Prida produkt</div>
                </div>
                <Footer />
            </div>
        )
    }
}

export default withRouter(Admin);