import React from "react";
import { Link, withRouter } from "react-router-dom";

import { addToCart, API_URL, shop } from "../config/config";
import Api from "../config/Api";

import Header from "../components/Header";
import Footer from "../components/Footer";
import Loading from "../components/Loading";
import Categories from "../components/Categories";

import "../styles/shop.css";

class Shop extends React.Component {

    state = {
        offset: 0,

        products: null,

        title: "",

        type: 1,
        category: 1,
        limit: 8,
        search: "",
        price: 0,
        soldAmount: true,

        loading: true
    }

    constructor() {
        super();

        this.loadData = this.loadData.bind(this)
        this.showMore = this.showMore.bind(this)
        this.openTypeAndCategory = this.openTypeAndCategory.bind(this)
        this.openPopular = this.openPopular.bind(this)
        this.changeFilters = this.changeFilters.bind(this)
        this.showCategories = this.showCategories.bind(this)
        this.hideCategories = this.hideCategories.bind(this)
        this.handleKeyPress = this.handleKeyPress.bind(this)
    }

    async loadData() {
        this.setState((state) => ({ title: shop[state.type - 1].categories[state.category - 1] }))
        this.setState({ loading: true })

        var filters = {
            filters: {
                type: this.state.type,
                category: this.state.category
            },
            sortBy: {

            },
            limit: this.state.limit,
        }

        if (this.state.search.trim() !== "") {
            filters["query"] = this.state.search
        }

        if (this.state.price === 1 || this.state.price === -1) {
            filters["sortBy"]["price"] = this.state.price
        }

        if (this.state.soldAmount) {
            delete filters["filters"]
            filters["sortBy"]["soldAmount"] = 1

            this.setState({ title: "Najpredvanejie" })
        }

        let products = await Api.getProducts(filters)

        if (this.state.products != products) {
            this.setState({ products: products, loading: false })
        }
    }

    handleKeyPress(event) {
        if (event.key === "Enter") {
            this.loadData();
        }
    }

    changeFilters(price) {
        this.setState({ price: price }, () => {
            this.loadData()
        })
    }

    openTypeAndCategory(type, category) {
        this.setState({
            type: type,
            category: category,
            soldAmount: false
        }, () => {
            this.loadData()
        });
    }
    
    openPopular() {
        this.setState({ soldAmount: true }, () => {
            this.loadData()
        })
    }

    showMore() {
        const limit = this.state.filters.limit

        this.setState((state) => ({ filters: {
            ...state.filters,
            limit: state.limit + 4
        }}), () => {
            this.loadData();
        })
    }

    showCategories() {
        var categories = document.getElementById("category-panel");
        categories.style.display = "inline-block";
    }

    hideCategories() {
        var categories = document.getElementById("category-panel");
        categories.style.display = "none";
    }

    componentDidMount() {
        this.setState({ offset: document.getElementById("header").clientHeight });
        window.addEventListener('resize', this.updateOffset.bind(this));

        this.loadData();
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.updateOffset.bind(this));
    }

    updateOffset() {
        this.setState({ offset: document.getElementById("header").clientHeight });
        this.forceUpdate();
    }

    render() {
        const products = this.state.products ? this.state.products.products : []
        const count = this.state.products ? this.state.products.count : 0;

        return(
            <div className="screen" id="shop">
                <Header />

                <div className="content" style={{ paddingTop: this.state.offset }}>
                    <div className="category-panel" id="category-panel">
                        <div style={{ width: "100%" }}>
                            <Categories openTypeAndCategory={this.openTypeAndCategory} openPopular={this.openPopular} changeFilters={this.changeFilters} hideCategories={this.hideCategories} />
                        </div>
                    </div>

                    <div className="product-panel">
                        <div className="upper-panel">
                            <div className="title">{this.state.title}</div>
                            <div className="text">
                                Vyberte si z naej irokej ponuky esencilnych olejov, ktor Vm nielen pomu prekona mnostvo ochoren, ale taktie Vm sprjemnia V kadodenn ivot.
                            </div>
                        </div>

                        <div className="filters">
                            <input className="field" type="text" onKeyPress={this.handleKeyPress} value={this.state.search} placeholder="Vyhadvanie" onChange={(event) => this.setState({ search: event.target.value })} />
                            <div className="button-filled" onClick={() => this.loadData()}>Hada</div>
                        </div>

                        <div className="button-filled" id="filters-button" onClick={() => this.showCategories()}>Filtre</div>

                        {this.state.loading ? (<div className="products"><Loading /></div>) : (
                            <div>
                                {count > 0 ? (
                                    <div className="products">
                                        {products.map((product) => <Product product={product} parent={this} /> )}
                                    </div>
                                ) : (
                                    <div className="empty-message">Nenali sa iadne vsledky</div>
                                )}
        
                                {this.state.limit <= count ? (<div className="button-filled" id="more-button" onClick={() => this.showMore()}>Zobrazi viac</div>) : null}
                            </div>
                        )}
                    </div>
                </div>

                <Footer />
            </div>
        )
    }
}

function Product(props) {

    const src = API_URL + "/uploads/" + props.product.imagePath

    return(
        <div className="product">
            <Link to={"/e-shop/" + props.product.name + "-" + props.product._id}><img className="image" src={src} /></Link>
            <div className="name">{props.product.name}</div>
            <div className="description">{props.product.description}</div>

            <div className="panel">
                <div className="price">{props.product.price / 100}</div>
                <div className="button-filled" onClick={() => addToCart(props.product._id, 1, props.parent)}>Do koka</div>
            </div>
        </div>
    )
}

export default withRouter(Shop);